@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-family: 'Mandhor';
  letter-spacing: 0.03em;
  line-height: 1.6195;
  font-family: 'Poppins', sans-serif;
}

.card__info h1 {
  padding-bottom: 10px;
  font-size: calc(16pt + 0.15vw);
}

.card__info p {
  font-size: calc(10pt + 0.15vw);
}

.cards {
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 30px 0px 30px 0px;
  text-align: center;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px;
  transition: .3s ease-in-out;
  -webkit-transition: .3s ease-in-out;
  -moz-transition: .3s ease-in-out;
  -ms-transition: .3s ease-in-out;
  -o-transition: .3s ease-in-out;
  border-radius: 20px;
  background-color: #cccccc;
  animation: nextarrow 3s infinite;
  -webkit-animation: nextarrow 3s infinite;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
}

#Svg__Wave {
  position: absolute;
  left: 0;
  bottom: 0;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.card img {
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  margin-top: -30px;
}

.cards:hover {
  animation: nextarrow 3s infinite;
  cursor: pointer;
  -webkit-animation: nextarrow 3s infinite;
}

@keyframes nextarrow {
  0%, 100% {
    transform: translateY(0);
    -webkit-transform: translateY(0);
    -moz-transform: translateY(0);
    -ms-transform: translateY(0);
    -o-transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
    -webkit-transform: translateY(10px);
    -moz-transform: translateY(10px);
    -ms-transform: translateY(10px);
    -o-transform: translateY(10px);
  }
}

.Underline {
  color: #423F8D;
  border-bottom: 3px solid #423F8D;
}

.Cards__Jsx {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 2rem 1fr 2rem 1fr;
  grid-template-columns: repeat(4, 1fr);
  margin: 20px 20px 20px 20px;
  grid-gap: 2rem;
}

.btn {
  padding: 20px 0px 20px 0px;
}

.btn button {
  padding: 7px 15px 7px 15px;
  cursor: pointer;
  border: none;
  background-color: #423F8D;
  color: white;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
  font-size: calc(8pt + 0.15vw);
}

nav {
  background-color: #423F8D;
  text-align: center;
}

nav p {
  color: white;
}

footer {
  text-align: center;
  background-color: #423F8D;
  color: white;
}

.Head {
  background-color: #ffff;
  text-align: center;
  margin: 50px 0px 50px 0px;
}

.Head h1 {
  font-size: calc(20pt + 0.15vw);
  padding: 15px 0px 15px 0px;
}

.Head h1 #Animated__Bg {
  position: relative;
  padding: 0px 10px 0px 10px;
}

.Head h1 #Animated__Bg::after {
  content: "";
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  height: 40%;
  opacity: .56;
  background-color: #423F8D;
  transition: .3s ease-in-out;
  -webkit-transition: .3s ease-in-out;
  -moz-transition: .3s ease-in-out;
  -ms-transition: .3s ease-in-out;
  -o-transition: .3s ease-in-out;
}

.Head h1 #Animated__Bg:hover::after {
  content: "";
  width: 100%;
  position: absolute;
  bottom: 0;
  cursor: pointer;
  left: 0;
  height: 100%;
  opacity: .56;
  transition: .5s ease-in-out;
  background-color: #423F8D;
  -webkit-transition: .5s ease-in-out;
  -moz-transition: .5s ease-in-out;
  -ms-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
}

@media screen and (max-width: 1080px) {
  .Cards__Jsx {
    -ms-grid-columns: 1fr;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 640px) {
  .Head h1 {
    font-size: calc(14pt + 0.15vw);
  }
}

@media screen and (max-width: 568px) {
  .Cards__Jsx {
    -ms-grid-columns: 1fr;
    grid-template-columns: repeat(1, 1fr);
  }
}